import * as React from "react";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";
import { isEmpty } from "lodash";
import {
  totalParticipants,
  income,
} from "../../../content/ambassadors-report/metrics";

interface Props {}
const Income: React.FC<Props> = () => {
  const [value, setValue] = React.useState({});

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={[colors.greyDark, colors.blueDark, colors.blue]}
        animation
        data={income}
        width={300}
        height={300}
        showLabels
        onValueMouseOver={v => {
          setValue({
            label: income.find(item => item.label === v.label).label,
            total:
              totalParticipants *
              (income.find(item => item.label === v.label).angle * 0.01),
            percentage: income.find(item => item.label === v.label).angle,
          });
        }}
        onSeriesMouseOut={(v: any) => setValue({})}
      >
        {!isEmpty(value) && <Hint value={value} />}
      </RadialChart>
    </div>
  );
};

export default Income;
